@import url("https://fonts.googleapis.com/css?family=Quicksand:400,700");
* {
  margin: 0 auto;
  padding: 0;
}

body {
  background-color: #606b9e;
  height: 100vh;
  font-family: 'Quicksand', sans-serif;
}

.logo-big {
  width: 300px;
  margin-bottom: 20px;
}

.form-horizontal .form-group {
  margin-right: 0px;
  margin-left: 0px;
}

.input-data {
  padding: 4%;
  font-size: 1.5em;
}

.sing-in {
  max-width: 200px;
  border: white 3px solid;
  background: transparent;
  border-radius: 0;
  padding: 1%;
  font-size: 1.5em;
  color: white;
  display: block;
  margin: 10px 0;
  position: relative;
  left: 44%;
}

.line-block {
  display: block;
  text-align: center;
}

.sing-in:hover {
  background-color: white;
  color: #606b9e;
  border: white 3px solid;
}

.text-navlink {
  font-size: 1.3em;
  color: white;
  opacity: 0.5;
}

.text-navlink:hover {
  font-size: 1.3em;
  color: white;
  opacity: 1;
}

header {
  height: 50px;
  background-color: #414c7e;
  padding: 3px 0;
}

.logo-small {
  height: 44px;
}

.txt-white {
  color: white;
}

.team {
  overflow: auto;
}

.board {
  background-color: #8e98c6;
  padding: 5px;
  text-justify: center;
  min-width: 200px;
  max-width: 200px;
  margin: 10px;
  border-radius: 10px;
}

.board-name {
  float: left;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  background: white;
}

::-webkit-scrollbar-track {
  background: #f5c2bc;
}

button {
  border: none;
}

.card {
  background-color: white;
  padding: 10px;
  margin: 5px 0;
  text-align: center;
  border-radius: 5px;
}

.card p {
  margin: 0;
  padding: 0;
  font-weight: 1.4em;
}

.card-add {
  background-color: #501daf;
  padding: 2px;
  margin: 5px 0;
  text-align: center;
  border-radius: 5px;
  color: white;
}

.name-user {
  color: #8e98c6;
  font-size: 1.2em;
  font-weight: bold;
  text-transform: uppercase;
  display: inline;
  margin-right: 12px;
}

.sign-out {
  border: none;
  border-radius: 5px;
  background-color: #fda69c;
  font-size: 1.2em;
  padding: 3px;
  display: inline;
}

.header-user {
  position: absolute;
  right: 1%;
  top: 1%;
  display: inline;
}

.input-add {
  width: 100%;
  padding: 5px;
  border: none;
  border-radius: 5px;
  margin-bottom: 3px;
}

.button-add {
  width: 50%;
  padding: 5px;
  border-radius: 5px;
  background-color: #46bdf5;
}

.button-add h4 {
  margin: 0;
  padding: 0;
}

.form-boards, .form-lists {
  min-width: 200px;
  background-color: #501daf;
  padding: 10px;
  margin: 0;
  float: left;
  border-radius: 10px;
}

.form-cards {
  width: 100%;
  padding: 10px;
  margin: 0;
  float: left;
  border-radius: 10px;
}

.form-cards.button-add {
  background-color: #46bdf5;
}

.board-actual {
  overflow: auto;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-overflow-scrolling: touch;
}


